import React, { Component, Fragment } from 'react'


import {Container, Row, Col,} from "react-bootstrap";
import HomeSlider from './HomeSlider';
import MegaMenu from "./MegaMenu";
export class HomeTop extends Component {
  render() {
       var settings = {
           dots: true,
           infinite: false,
           speed: 500,
           slidesToShow: 4,
           slidesToScroll: 4,
           initialSlide: 0,
           responsive: [
               {
                   breakpoint: 1024,
                   settings: {
                       slidesToShow: 3,
                       slidesToScroll: 3,
                       infinite: true,
                       dots: true,
                   },
               },
               {
                   breakpoint: 600,
                   settings: {
                       slidesToShow: 2,
                       slidesToScroll: 2,
                       initialSlide: 2,
                   },
               },
               {
                   breakpoint: 480,
                   settings: {
                       slidesToShow: 1,
                       slidesToScroll: 1,
                   },
               },
           ],
       };
    return (
        <Fragment>
            <Container className='p-0 m-0 overflow-hidden' fluid={true}>
                <Row>
                    <Col lg={3} md={3} sm={12} >
                        <MegaMenu></MegaMenu>
                    </Col>
                    <Col lg={9} md={9} sm={12}>
                        <HomeSlider></HomeSlider>
                    </Col>
                </Row>
            </Container>
        </Fragment>
    );
  }
}

export default HomeTop