import React, {Component, Fragment} from "react";
import {Link} from "react-router-dom";

import {Navbar, Container, Row, Col, Button} from "react-bootstrap";

import Logo from "../../assets/images/easyshop.png";
import MegaMenuMobile from "../home/MegaMenuMobile";

export class NavMenuMobile extends Component {
    constructor() {
        super();
        this.state = {
            SideNavState: "sideNavClose",
            ContentOverState: 'ContentOverlayClose'
        };
    }
    MenuBarClickHandler= () => {
      this.SideNavOpenClose();

    }
    ContentOverlayClickHandler = () => {
      this.SideNavOpenClose();

    }
    SideNavOpenClose= () => {
      let SideNavState = this.state.SideNavState;
      let ContentOverState = this.state.ContentOverState
      if(SideNavState==='sideNavOpen') {
        this.setState({SideNavState:'sideNavClose', ContentOverState:'ContentOverlayClose'})
      } else {
        this.setState({SideNavState: "sideNavOpen", ContentOverState: 'ContentOverlayOpen'});
      }
    }
    render() {
        return (
            <Fragment>
                <div className="TopSectionDown"></div>
                <Navbar fixed={"top"} className="navbar" bg="light">
                    <Container fluid={"true"} className="p-2 mb-0 fixed-top shadow-sm bg-white">
                        <Row>
                            <Col lg={4} md={4} sm={12} xs={12}>
                                <Link to="/">
                                    <Button className="btn" onClick={this.MenuBarClickHandler}>
                                        <i className="fa fa-bars"></i>
                                    </Button>
                                    <img className="nav-logo" src={Logo} alt="" />
                                    <Button className="cart-btn">
                                        <i className="fa fa-shopping-cart"></i> 3 Items
                                    </Button>
                                </Link>
                            </Col>
                        </Row>
                    </Container>
                    <div className={this.state.SideNavState}>
                       <MegaMenuMobile></MegaMenuMobile>
                    </div>
                    <div onClick={this.ContentOverlayClickHandler} className={this.state.ContentOverState}></div>
                </Navbar>
            </Fragment>
        );
    }
}

export default NavMenuMobile;
