import React, {Component, Fragment} from "react";

export class MegaMenuMobile extends Component {
    constructor() {
        super();
        this.MegaMenu = this.MegaMenu.bind(this);
    }

    componentDidMount() {
        this.MegaMenu();
    }

    MegaMenu() {
        let acc = document.getElementsByClassName("accordionMobile");
        let accNumber = acc.length;
        let i;
        for (i = 0; i < accNumber; i++) {
            acc[i].addEventListener("click", function () {
                this.classList.toggle("active");
                let panel = this.nextElementSibling;
                if (panel.style.maxHeight) {
                    panel.style.maxHeight = null;
                } else {
                    panel.style.maxHeight = panel.scrollHeight + "px";
                }
            });
        }
    }
    render() {
        return (
            <Fragment>
                <div className="accordionMenuDivMobile">
                    <div className="accordionMenuDivInsideMobile">
                        <button className="accordionMobile">
                            <img
                                className="accordionMenuIconMobile"
                                src="https://image.flaticon.com/icons/png/128/739/739249.png"
                                alt=""
                            />
                            &nbsp;Man's Clothing
                        </button>
                        <div className="panelMobile">
                            <ul>
                                <li>
                                    <a href="#" className="accordionItemMobile">
                                        Mans Tshirt 1
                                    </a>
                                </li>
                                <li>
                                    <a href="#" className="accordionItemMobile">
                                        Mans Tshirt 2
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default MegaMenuMobile;
